@keyframes Left {
    0% {
      left: 0%;
        transform: rotate(0deg);
    }
    50% {
      left: 25%;
      transform: rotate(360deg);
    }
    100% {
      left: 0%;
      transform: rotate(0deg);
    }
}
@keyframes Right {
    0% {
      right: 0%;
      transform: rotate(0deg);
    }
    50% {
      right: 25%;
      transform: rotate(360deg);
    }
    100% {
      right: 0%;
      transform: rotate(0deg);
    }
}
.animation-left {
    position: absolute;
    left: 0px;
    animation: Left 4s infinite ; 
}

.animation-right {
    position: absolute;
    right: 0px;
    animation: Right 4s infinite;
}


